<template>
  <div class="page">
    <div class="container">
      <div class="gashapon" :style="wheelSize">
        <mk-gashapon
          ref="gashapon"
          v-bind="baseConfig"
          @stateChange="stateChange"
          @clickStart="clickStart"
        />
        <div class="info">
          <p>当前状态：{{ state }}</p>
        </div>
      </div>
    </div>
    <demo-block card title="基础操作">
      <button
        class="common-button primary"
        :disabled="!canStart"
        @click="clickStart"
      >
        开始
      </button>
      <button class="common-button primary" @click="reset">重置</button>
      <button class="common-button primary" @click="disable">禁用</button>
      <button
        v-for="(item, index) in baseConfig.smallSpecialBallArray"
        :key="index"
        class="common-button primary"
        :disabled="!canStop"
        @click="stop(index)"
      >
        小球{{ index + 1 }}
      </button>
    </demo-block>
  </div>
</template>
<script>
import { StateConstant } from '@tuia/market-ui';

export default {
  data() {
    return {
      state: 0,
      baseConfig: Object.freeze({
        smallSpecialBallArray: [
          '//yun.tuisnake.com/market-ui/0b0e64df-9952-42aa-82ad-6674bb7d9334.png',
          '//yun.tuisnake.com/market-ui/09d29acc-8460-440f-9470-0451de9f2320.png',
          '//yun.tuisnake.com/market-ui/5ec21e4a-515e-4e64-a75f-2b5c2f78e4d0.png',
          '//yun.tuisnake.com/market-ui/421580be-5191-422c-9e7d-a98b0ccefca5.png',
          '//yun.tuisnake.com/market-ui/89f51532-5bbf-4e5b-8fc3-6e09cead7655.png',
        ],
      }),
      wheelSize: {},
    };
  },

  computed: {
    canStart() {
      return this.state === StateConstant.WAIT_START;
    },
    canStop() {
      return this.state === StateConstant.WAIT_END;
    },
  },

  mounted() {
    const { width, height } = this.$refs.gashapon.$el.style;
    this.wheelSize = { width, height };
  },

  methods: {
    stateChange(opt) {
      this.state = opt.now;
    },

    async clickStart() {
      const gashapon = this.$refs.gashapon;
      if (!gashapon.isWaitStart) {
        return;
      }
      await gashapon.start();
    },

    async stop(index) {
      await this.$refs.gashapon.stop({ index });
    },

    async reset() {
      await this.$refs.gashapon.reset();
    },

    disable() {
      this.$refs.gashapon.disable();
    },
  },
};
</script>
